<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/youtube')">유튜브</li>
			<li @click="goto('/admin/column')">칼럼</li>
			<li @click="goto('/admin/writer')" class="current">칼럼 작성자</li>
		</ul>

		<div class="contents-container">
			<div class="tab-contents">
				<ul class="table board">
					<li class="row props">
						<div class="prop check">
							<input
								type="checkbox"
								class="checkbox"
								name="member"
								id="select-all"
							/>
							<label for="select-all"></label>
						</div>
						<div class="prop small">작성자</div>
						<div class="prop small">직업</div>
						<div class="prop large">상세 설명</div>
						<div class="prop icon">수정</div>
					</li>
					<slot v-if="!writerList">
						<li class="row no-data body3" style="display: block;">
							등록된 글이 없습니다
						</li>
					</slot>
					<slot v-else>
						<li v-for="item in writerList" :key="item.id" class="row">
							<div class="prop check">
								<input
									type="checkbox"
									class="checkbox"
									name="member"
									:id="item.id"
									:value="item.id"
									v-model="selected"
								/>
								<label :for="item.id"></label>
							</div>
							<div class="prop small txt-left">
								{{ item.name }}
							</div>
							<div class="prop small txt-left">
								{{ item.job }}
							</div>
							<div class="prop large txt-left">
								{{ item.contents }}
							</div>
							<div class="prop">
								<router-link
									:to="{ name: 'EditWriter', params: { id: item.id } }"
								>
									<Edit />
								</router-link>
							</div>
						</li>
					</slot>
				</ul>
				<LoadingSpinner :loading="isLoading" />
				<div class="bottom-container">
					<Pagination
						v-if="totalPage !== -1"
						:listRowCt="15"
						:pageLinkCt="5"
						:totalCount="totalPage"
						:url="`/admin/writer`"
					/>
					<div class="btn-container">
						<div class="checked-info body3 marR-30">
							<span class="number">{{ selectedCnt }}</span
							><span>개 선택됨</span>
						</div>

						<button
							class="solid-btn red"
							@click="isDeleteModalViewed = true"
							:disabled="selected.length < 1"
						>
							선택 글 삭제
						</button>
						<router-link :to="{ name: 'AddWriter' }">
							<button class="solid-btn green">
								새 글 등록
							</button>
						</router-link>
					</div>
				</div>
				<ModalView
					v-if="isDeleteModalViewed"
					@close-modal="isDeleteModalViewed = false"
				>
					<DeleteItem
						@close-modal="isDeleteModalViewed = false"
						@confirm="deleteColumnWriter"
						:count="selected.length"
					></DeleteItem>
				</ModalView>
			</div>
		</div>
	</div>
</template>
<script>
import { fetchColumnWriter, deleteColumnWriter } from '@/api/column';
import Edit from '@/assets/icon/Edit';
import Pagination from '@/components/common/Pagination';
import DeleteItem from '@/views/admin/notice-news/components/DeleteItem';
import ModalView from '@/components/common/ModalView';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	components: { LoadingSpinner, DeleteItem, Edit, ModalView, Pagination },
	created() {
		this.currentPage = this.$route.params.page;

		this.fetchColumnWriter(this.currentPage);
	},
	data() {
		return {
			writerList: [],
			selected: [],
			totalPage: -1,
			currentPage: 1,
			isLoading: false,
			isDeleteModalViewed: false,
		};
	},
	computed: {
		selectedCnt() {
			return this.selected.length;
		},
		selectAll: {
			get: function() {
				return this.writerList
					? this.selected.length == this.writerList.length
					: false;
			},
			set: function(value) {
				var selected = [];

				if (value) {
					this.writerList.forEach(function(item) {
						console.log(item.id);
						selected.push(item.id);
					});
				}
				this.selected = selected;
			},
		},
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchColumnWriter(page) {
			this.isLoading = true;
			try {
				const { data } = await fetchColumnWriter(page - 1);
				this.writerList = data;
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
		async deleteColumnWriter() {
			this.isLoading = true;

			try {
				await deleteColumnWriter(this.selected);
				this.fetchColumnWriter(this.currentPage);
				this.isDeleteModalViewed = false;
				this.selected = [];
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.fetchColumnWriter(this.currentPage);
			}
		},
	},
};
</script>
<style scoped>
.small {
	width: 160px;
}
.large {
	width: 563px;
}

.icon {
	width: 100px;
}
</style>
